export default function(updateElements, initDisplay, restartParallax) {

    // shim layer with setTimeout fallback
    window.requestAnimFrame = (function() {
        return (
            window.requestAnimationFrame ||
            window.webkitRequestAnimationFrame ||
            window.mozRequestAnimationFrame ||
            window.oRequestAnimationFrame ||
            window.msRequestAnimationFrame ||
            function(callback) {
                window.setTimeout(callback, 1000 / 60);
            }
        );
    })();

    (function(win, d, updateElements) {
        let $ = d.querySelector.bind(d);

        let ticking = false;
        let lastScrollY = 0;

        // resize end
        let rtime;
        let timeout = false;
        let delta = 200;


        function resizeend() {
            if (new Date() - rtime < delta) {
                setTimeout(resizeend, delta);
            } else {
                timeout = false;
                //updateElements(win.pageYOffset, prefix, pos, setTickingFalse);
                win.removeEventListener("resize", onResize, false);
                win.removeEventListener("scroll", onScroll, false);
                restartParallax();
                updateElements(lastScrollY, prefix, pos, setTickingFalse);

            }
        }

        function onResize() {

            rtime = new Date();
            if (timeout === false) {
                timeout = true;
                setTimeout(resizeend, delta);
            }

        }

        function setTickingFalse() {
            ticking = false;
        }

        function pos(base, range, relY, offset) {
            return base + limit(0, 1, relY - offset) * range;
        }

        function prefix(obj, prop, value) {
            let prefs = ["webkit", "Moz", "o", "ms"];
            for (let pref in prefs) {
                obj[prefs[pref] + prop] = value;
            }
        }

        function limit(min, max, value) {
            return Math.max(min, Math.min(max, value));
        }

        function onScroll(evt) {
            if (!ticking) {
                ticking = true;

                requestAnimFrame(() => { updateElements(lastScrollY, prefix, pos, setTickingFalse); });
                lastScrollY = win.pageYOffset;

            }
        };

        initDisplay();

        win.addEventListener("resize", onResize, false);
        win.addEventListener("scroll", onScroll, false);

        // fix initial placement of parallax elements
        updateElements(lastScrollY, prefix, pos, setTickingFalse);

        // perfect height fix for the parallay height
        $("#blob-container").style.height = document.body.scrollHeight + "px";
    })(window, document, updateElements);
};